.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: white;
  margin-left: auto;
  margin-right: auto;
  width: 85%;
  height: 70vh;
  opacity: 0; /* nascondi il contenuto all'inizio */
  transition: opacity 1s ease-in-out; /* animazione per visualizzare gradualmente il contenuto */
  margin-top: 50px;
  border-radius: 15px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.container.show {
  opacity: 1; /* mostra il contenuto */
}

@import url("https://fonts.googleapis.com/css?family=Poppins");
.text-container {
  color: black;
  font-family: Poppins;
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  animation-name: slide-up;
  animation-duration: 1s;
  animation-fill-mode: both;
}

.image-container {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  animation-name: fade-in;
  animation-duration: 2s;
  animation-delay: 1s;
  animation-fill-mode: both;
}

.image-container .profile-image {
  width: 400px;
  height: 400px;
  border-radius: 50%;
}

h1 {
  font-style: italic;
  font-size: 4rem;
  margin-bottom: 2rem;
}

h2 {
  font-size: 3rem;
  margin-top: 0;
}

.profile-image {
  max-width: 80%;
  border-radius: 50%;
}

@media screen and (min-width: 768px) {
  .container {
    flex-direction: row;
  }

  .text-container {
    order: 1;
    margin-right: 3rem;
    text-align: center;
  }

  .image-container {
    order: 2;
    margin-left: 3rem;
  }

  .profile-image {
    max-width: 60%;
  }
}

@keyframes slide-up {
  from {
    transform: translateY(50%);
    opacity: 0;
  }
  to {
    transform: translateY(0%);
    opacity: 1;
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@media screen and (max-width: 768px) {
  .container {
    flex-direction: column;
    height: auto;
  }

  .text-container {
    order: 2;
    margin-right: 0;
    margin-bottom: 2rem;
  }

  .image-container {
    order: 1;
    margin-left: 0;
  }

  .profile-image {
    max-width: 80%;
  }
}
