@import url(https://fonts.googleapis.com/css?family=Poppins);

.projects {
  width: 80%;
  margin: auto;
  display: block;
  text-align: center;
}

.project-page {
  position: relative;
  width: 65%;
  margin: auto;
  overflow: hidden;
  margin-bottom: 50px;
  border-radius: 15%;
}

.project-page-image {
  margin: auto;
  width: 100%;
  border-radius: 15%;
  box-shadow: 0 0 0 5px rgb(0 0 0 / 30%);
}

.project-page .description {
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  padding: 20px;
  box-sizing: border-box;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: opacity 0.5s ease;
  border-radius: 15%;
  text-align: center;
  font-size: 26px;
  font-family: Poppins;
}

.project-page:hover .description {
  opacity: 1;
  font-size: 20px;
}

.page-info {
  width: 25%;
  margin: auto;
}

.page-title {
  padding: 5px;
  border-radius: 15%;
  background-color: white;
  text-align: center;
  font-size: 100px;
  font-weight: bold;
  margin-top: 80px;
  font-family: Poppins;
}

.page-subtitle {
  text-align: center;
  font-size: 26px;
  font-weight: bold;
  font-family: Poppins;
}

.project-title {
  margin: auto;
  margin-top: 150px;
}

.project-a:hover {
  background-color: black;
  color: white;
  transition: 0.5s ease;
}

.project-a {
  width: 20%;
  background-color: #fff;
  border-radius: 15px;
  text-decoration: none;
  color: black;
  font-size: 24px;
  max-width: 100%;
  display: inline-block;
  overflow: hidden;
  font-family: Poppins;
  font-weight: bold;
  transition: 0.5s ease;
  cursor: default;
}

/* Schermi con larghezza massima di 768px */
@media screen and (max-width: 768px) {
  .page-info {
    width: fit-content;
  }
  .page-title {
    font-size: 50px;
  }
  .project-page {
    width: 90%;
  }

  .project-page-image {
    width: 90%;
  }

  .project-a {
    font-size: 10px;
  }

  .project-page .description {
    font-size: 10px;
  }
}

/* Schermi con larghezza massima di 576px */
@media screen and (max-width: 576px) {
  .project-page {
    width: 100%;
  }

  .project-page-image {
    width: 100%;
  }
}
