.info.show {
  opacity: 1;
  transform: translateY(0);
  transition: opacity 1s, transform 1s;
}

.info {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 1s, transform 1s;
}

.info-title {
  text-align: center;
  font-family: Poppins;
  margin-top: 200px;
  text-shadow: 4px 0 #fff, -4px 0 #fff, 0 4px #fff, 0 -4px #fff, 2px 2px #fff,
    -2px -2px #fff, 2px -2px #fff, -2px 2px #fff;
  font-size: 60px;
}

.about-container {
  border-radius: 15px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  background-color: #f7f7f7;
  padding: 30px;
  max-width: 1200px; /* imposta la larghezza massima del contenitore */
  margin: 0 auto; /* centra il contenitore sulla pagina */
  margin-bottom: 50px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

@import url("https://fonts.googleapis.com/css?family=Poppins");
.about-text {
  font-family: Poppins;
  flex-basis: 100%;
  margin-bottom: 30px;
  font-size: 26px;
  text-align: center;
  max-width: 900px;
  width: 100%; /* imposta la larghezza del contenitore interno sulla larghezza massima del contenitore esterno */
}

.bold {
  font-weight: bold;
  color: black;
}

.about-svg {
  flex-basis: 100%;
  margin-bottom: 30px;
}

@media only screen and (min-width: 768px) {
  .about-text {
    flex-basis: 50%;
    margin-bottom: 0;
    margin-right: 10px;
    text-align: left;
    width: auto; /* reimposta la larghezza del contenitore interno a "auto" */
    max-width: none; /* rimuovi la larghezza massima del contenitore interno */
  }

  .about-svg {
    flex-basis: 40%;
    margin-bottom: 0;
    margin-left: 10px;
    padding: 10px;
  }

  .about-svg svg {
    width: 100%;
    height: auto;
  }

  .about-container {
    width: 80%;
  }
}

@media only screen and (max-width: 768px) {
  .about-text {
    font-size: 18px;
  }
  .about-svg svg {
    width: 100%;
    height: auto;
  }
  .about-container {
    width: 80%;
  }
}
