h2 {
  font-size: 3rem;
}

.project-section.show {
  opacity: 1;
  transform: translateY(0);
  transition: opacity 1s, transform 1s;
}

.project-section {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 1s, transform 1s;
}

.project-section-title {
  text-align: center;
  font-family: Poppins;
  margin-top: 200px;
  text-shadow: 4px 0 #fff, -4px 0 #fff, 0 4px #fff, 0 -4px #fff, 2px 2px #fff,
    -2px -2px #fff, 2px -2px #fff, -2px 2px #fff;
}

.project-section-container {
  border-radius: 15px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  background-color: white;
  padding: 30px;
  max-width: 1200px; /* imposta la larghezza massima del contenitore */
  margin: 0 auto; /* centra il contenitore sulla pagina */
  margin-bottom: 50px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

@import url("https://fonts.googleapis.com/css?family=Poppins");
.project-section-text {
  font-family: Poppins;
  flex-basis: 100%;
  margin-bottom: 30px;
  font-size: 26px;
  text-align: left;
  max-width: 900px;
  width: 100%; /* imposta la larghezza del contenitore interno sulla larghezza massima del contenitore esterno */
}

.project-section-link {
  font-family: Poppins;
  font-size: 26px;
  text-decoration: none;
  color: black;
  margin-top: 30px;
  margin-bottom: 20px;
}

.here {
  text-decoration: none;
  color: black;
}

.here:hover {
  color: rgb(255, 193, 0);
}

.bold {
  font-weight: bold;
  color: black;
}

.project-section-svg {
  flex-basis: 100%;
  margin-bottom: 30px;
  text-align: right;
}

@media only screen and (min-width: 768px) {
  .project-section-text {
    flex-basis: 50%;
    margin-bottom: 0;
    margin-right: 10px;
    text-align: left;
    width: auto; /* reimposta la larghezza del contenitore interno a "auto" */
    max-width: none; /* rimuovi la larghezza massima del contenitore interno */
  }

  .project-section-svg {
    flex-basis: 40%;
    margin-bottom: 0;
    margin-left: 10px;
    padding: 10px;
  }

  .project-section-svg svg {
    width: 120%;
    height: auto;
  }
  .project-section-span {
    text-decoration: none;
  }
}

@media only screen and (max-width: 768px) {
  .project-section-container {
    width: 80%;
  }
  .project-section-text {
    font-size: 18px;
  }
  .here {
    font-size: 18px;
  }

  .project-section-link {
    font-size: 18px;
  }
}
